import {SelectItem, onItemSelected} from "../../ui/select";
export default function (set:any) {

    return {
        text: set.name,
        value: set.id,
        createItem(instance, item) : HTMLDivElement {
            let option:HTMLDivElement = document.createElement('div');

            item.element = option;

            option.innerHTML = '<div>' + item.text + '</div><div class="item-description">' + item.metadata.count + '</div>';
            option.setAttribute('data-value', item.value);

            option.classList.add('select-option');

            option.addEventListener('click', onItemSelected.bind({
                instance: instance,
                current: item
            }));

            if (item.isSelected === true) {
                option.classList.add('active');
            }

            return option;
        },
        element: null,
        isSelected: typeof set.selected !== "undefined" ? set.selected : false,
        metadata: set
    } as SelectItem;

}